
.button-hamburgerIcon {
  width: 30px;
  border: none;
  cursor: pointer;
  outline: none;
  display: block;
  margin-top: 15px;
  margin-left: auto;
  margin-right: auto; 
  
}

.button-hamburgerIcon .img-hamburger{
  position: fixed;
    height: 32px;
    vertical-align: middle;
}

.button-hamburgerIcon:active{
      transform: scale(0.90);
}



