/** @format Nabvar*/

* {
  margin: 0;
  font-family: "Noto Sans", sans-serif;
}
.header-navbar {
  height: 64px;
  background-color: #14343f;
  box-shadow: 1px 5px 4px 0 rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  position: fixed;
  width: 100%;
  z-index: 3;
}
/*Propiedades de los containers*/
.header-navbar .navbarDiv1 {
  display: block;
  float: left;
  padding: 0;
  margin: 0;
  height: 100%;
  width: 33.33%;
  overflow:hidden ;
  outline: none;
}
.header-navbar .navbarDiv2 {
  width: 33.33%;
  float: left;
  overflow: hidden;
  height: 64px;
 padding: auto;

}
.header-navbar .navbarDiv2 StarButton{
  display: block;
  
}

.header-navbar .navbarDiv3 {
  display: block;
  width: 33.33%;
  float: right;
  height: 100%;
}









/** @format */
.starInput {
  background-color: #ffc500;
  color: #fff7dc;
  width: auto;
  height: auto;
  border-radius: 7px;
  font-size: large;
  font-family: "Press Start 2P", cursive;
  letter-spacing: 3px;
  text-shadow: 2px -1px #5c553e;
  padding: 10px;
  box-shadow: 3px 5px #c6571b;
  border: none;
  cursor: pointer;
  outline: none;
  display: block;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto 
}

.starInput:active {
  color: #ffc500;
  background-color: #b08d1c;
  box-shadow: 0 2px #ffc500;
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
  transition-duration: 0.03s;
}
.starInput:hover{
  
}
.div-starbutton{
  display: block;
  float: right;
  width: 100%;
}


.button-hamburgerIcon {
  width: 30px;
  border: none;
  cursor: pointer;
  outline: none;
  display: block;
  margin-top: 15px;
  margin-left: auto;
  margin-right: auto; 
  
}

.button-hamburgerIcon .img-hamburger{
  position: fixed;
    height: 32px;
    vertical-align: middle;
}

.button-hamburgerIcon:active{
      -webkit-transform: scale(0.90);
              transform: scale(0.90);
}



